/* MOBILE VIEW!! */
@media only screen and (max-width: 1024px) {
  .article-title {
    margin-left: 0; }
  #title {
    padding: 10px;
    width: 100%;
    max-width: 100%; }
  .content {
    padding: 10px; }
  #resultDiv {
    width: 100%; }
  .left-lane {
    position: inherit;
    width: 100%; }
  table {
     width: 100%; } }

.warn {
  animation-name: warning;
  animation-duration: 0.5s; }

@keyframes warning {
  0% {
    color: white; }
  5% {
    color: red; }
  100% {
    color: white; } }

@media only screen and (max-width: 768px) {
  .proj-title,
  .search,
  .btn-search {
    font-size: 1.5rem; }
  .search {
    margin-left: 0;
    margin-right: 20px;
    float: right;
    height: 30px;
    max-width: calc(30% - 30px); }
  .btn-search {
    position: absolute;
    right: 0;
    padding-right: 0;
    top: 10px; } }
